import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {ChangePasswordRequest} from 'src/app/core/models/customer/customer/request/ChangePasswordRequest';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {CustomerAccountService} from 'src/app/core/services/auth/customer/customer-account.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {SpinnerService} from 'src/app/core/services/comon-services/spinner/spinner.service';

@Component({
	selector: 'app-customer-change-password',
	templateUrl: './customer-change-password.component.html',
	styleUrls: ['./customer-change-password.component.less'],
	providers: [DestroyService]
})
export class CustomerChangePasswordComponent implements OnInit {
	submitted = false; //return true if submitted
	returnUrl = ''; //the url that is used when submit and change password successfully and redirect to login page
	customerChangePasswordForm: FormGroup; //form Group of information fields
	//host: string = environment.apiUrl; //host of server
	constructor(
		private formBuilder: FormBuilder,
		private destroy$: DestroyService,
		private appErrorHandler: AppErrorHandler,
		private accountService: CustomerAccountService,
		private notificationService: NotificationService,
		private spinnerService: SpinnerService
	) {
		//init form
		this.customerChangePasswordForm = this.formBuilder.group({
			oldPassword: ['', [Validators.required, Validators.minLength(3), Validators.pattern]],
			newPassword: ['', [Validators.required, Validators.minLength(3)]],
			newPasswordComfirm: ['', [Validators.required, Validators.minLength(3)]]
		});
	}

	ngOnInit(): void {}
	submitForm(): void {
		try {
			this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
			const val = this.customerChangePasswordForm.value;
			const changePasswordRequest: ChangePasswordRequest = {
				oldPassword: val.oldPassword,
				newPassword: val.newPassword,
				newPasswordComfirm: val.newPasswordComfirm
			};
			this.accountService
				.customerChangePassword(changePasswordRequest)
				.pipe(takeUntil(this.destroy$))
				.subscribe(
					(res) => {
						if (res) {
							if (res.isSuccessed) {
								if (res.message) {
									this.notificationService.showSuccessNotification(res.message);
								}
							} else {
								if (res.message) {
									this.notificationService.showErrorNotification(res.message);
								}
							}
						}
						this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
					},
					(error) => {
						this.appErrorHandler.handleErrorWithNoti(error);
						this.spinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
					}
				);
		} catch (error) {
			this.appErrorHandler.handleError(error);
		}
	}
}
